import React from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import Button from "@material-ui/core/Button";
import { styles } from "./styles";

const CustomButton = ({
    classes,
    className,
    grouped,
    addRemove,
    color,
    ariaLabel,
    nextPrev,
    variant,
    size,
    fullWidth,
    disabled,
    href,
    target,
    onClick,
    icon,
    iconPosition,
    label,
    play,
    type,
}) => {
    const isGrouped = group => {
        if (group) {
            if (group === 1) {
                return classes.grouped1;
            }
            return classes.grouped2;
        }
        return "";
    };

    const customClass = `
        ${className}
        ${isGrouped(grouped)}
        ${addRemove && color === "primary" && classes.addRemovePrimary}
        ${addRemove && color === "secondary" && classes.addRemoveSecondary}
          ${nextPrev && classes.nextPrev}
          ${play && classes.play}
    `;
    return (
        <Button
            variant={variant}
            color={color}
            aria-label={ariaLabel}
            classes={{
                root: classes.root,
                label: classes.label,
                containedPrimary: classes.containedPrimary,
                containedSecondary: classes.containedSecondary,
                textPrimary: classes.textPrimary,
                outlinedPrimary: classes.outlinedPrimary,
                outlinedSecondary: classes.outlinedSecondary,
                sizeSmall: classes.sizeSmall,
                sizeLarge: classes.sizeLarge,
            }}
            className={customClass}
            size={size}
            fullWidth={fullWidth}
            disabled={disabled}
            href={href}
            target={target}
            onClick={onClick}
            type={type}
        >
            {icon && iconPosition === "before" && icon}
            <span className={icon && label ? classes[iconPosition] : ""}>
                {label}
            </span>
            {icon && iconPosition === "center" && icon}
            {icon && iconPosition === "after" && icon}
        </Button>
    );
};

CustomButton.propTypes = {
    classes: PropTypes.oneOfType([PropTypes.object, PropTypes.string]),
    className: PropTypes.string,
    grouped: PropTypes.number,
    addRemove: PropTypes.bool,
    nextPrev: PropTypes.bool,
    variant: PropTypes.string,
    color: PropTypes.string,
    ariaLabel: PropTypes.string,
    size: PropTypes.string,
    fullWidth: PropTypes.bool,
    disabled: PropTypes.bool,
    href: PropTypes.string,
    target: PropTypes.string,
    onClick: PropTypes.func,
    icon: PropTypes.object,
    iconPosition: PropTypes.string,
    label: PropTypes.string,
    type: PropTypes.string,
    play: PropTypes.bool
};

export default withStyles(styles, { withTheme: true })(CustomButton);
